const msgContainer=document.querySelector(".message--container"),msgHideBtn=document.querySelector(".message__hide__btn");msgContainer&&msgHideBtn.addEventListener("click",(()=>{msgContainer.classList.add("message--hide")}));const firstname=document.querySelector(".firstname__field"),lastname=document.querySelector(".lastname__field"),nameForm=document.querySelector(".nameForm");nameForm.addEventListener("submit",(e=>{e.preventDefault(),fetch("/updateName",{headers:{"Content-Type":"application/json"},method:"POST",body:JSON.stringify({firstname:firstname.value,lastname:lastname.value})}).then((e=>e.text())).then((e=>e)).catch((e=>console.log(e.text()))),msgContainer.classList.remove("message--hide")}));const aboutUser=document.querySelector(".about_user"),address=document.querySelector(".address"),aboutForm=document.querySelector(".aboutForm");aboutForm.addEventListener("submit",(e=>{e.preventDefault(),fetch("/updateAboutUser",{headers:{"Content-Type":"application/json"},method:"POST",body:JSON.stringify({about_user:aboutUser.value,address:address.value})}).then((e=>e.text())).then((e=>e)).catch((e=>console.log(e.text()))),msgContainer.classList.remove("message--hide")}));